import { Field, InputType } from "@nestjs/graphql";
import { IsBoolean, IsEnum, IsNotEmpty, IsNumber, IsOptional, IsString } from "class-validator";
import { SubscriptionInterval } from "../../../../orm-config/entity/enum/subscription.interval";


@InputType()
export class RazorpaySubscriptionInput {

  @Field(() => SubscriptionInterval)
  @IsEnum(SubscriptionInterval)
  period: SubscriptionInterval;

  @Field(() => Number)
  @IsNotEmpty()
  @IsNumber()
  interval: number;

  @Field(() => String)
  @IsNotEmpty()
  @IsString()
  description: string;

  @Field(() => Number)
  @IsOptional()
  @IsNumber()
  quantity: number;

  @Field(() => Number)
  @IsOptional()
  @IsNumber()
  totalCount: number;

  @Field(() => Boolean)
  @IsOptional()
  @IsBoolean()
  customerNotify: boolean;

  @Field(() => Number)
  @IsNotEmpty()
  @IsNumber()
  amount: number;

  @Field(() => String)
  @IsNotEmpty()
  @IsString()
  currency: string;
  
}
